html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #FFFFFF;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  list-style: none;
}

.color-secundario {
  color: #B40202;
  text-decoration: underline;
}

.boton-flotante {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.boton-flotante a {
  background-color: #B40202;
  color: #FFFFFF;
  text-align: center;
  padding: 15px 20px;
  display: block;
  border-radius: 60%;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

/** Media Queries **/
.header {
  display: flex;
  flex-direction: column;
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  min-width: 100%;
  background: #FFFFFF;
  position: relative;
}
.navbar__box {
  padding: 2rem;
}
.navbar__redes {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.navbar__redes--enlace {
  margin: 0 0.8rem;
}
@media (min-width: 768px) {
  .navbar__redes--enlace {
    margin: 0 2rem;
  }
}
.navbar__redes--enlace ion-icon {
  color: #B40202;
  width: 5rem;
  height: 5rem;
}

.logo {
  min-width: 35%;
  max-width: 35%;
  min-height: 4rem;
}
.logo___imagen {
  height: 4rem;
}

#hover2 {
  position: relative;
}

.menu {
  min-width: 100%;
}
.menu ul {
  padding: 0;
}
.menu__lista {
  display: none;
  flex-direction: column;
  min-width: 100%;
  margin: 0;
}
@media (min-width: 1025px) {
  .menu__lista {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-around;
    background-color: #283891;
  }
}
.menu__boton {
  display: inline-block;
  border: 0.1rem solid #FFFFFF;
  width: 3.8rem;
  height: 3.8rem;
  position: absolute;
  z-index: 5;
  background-color: #283891;
  color: #FFFFFF;
}
@media (min-width: 280px) {
  .menu__boton {
    top: 36.39px;
  }
}
@media (min-width: 360px) {
  .menu__boton {
    top: 47.7px;
  }
}
@media (min-width: 390px) {
  .menu__boton {
    top: 51.72px;
  }
}
@media (min-width: 460px) {
  .menu__boton {
    top: 61.63px;
  }
}
@media (min-width: 470px) {
  .menu__boton {
    top: 63.64px;
  }
}
@media (min-width: 500px) {
  .menu__boton {
    top: 67px;
  }
}
@media (min-width: 1025px) {
  .menu__boton {
    display: none;
  }
}
.menu__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .menu__contenedor {
    width: 100%;
  }
}
.menu__contenedor--enlace {
  background-color: #283891;
  color: #FFFFFF;
  padding: 1.5rem 0;
  text-align: center;
  width: 100%;
}
.menu__contenedor--enlace:hover {
  background-color: #B40202;
}
.menu__contenedor--subcontenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100%;
}
@media (min-width: 1025px) {
  .menu__contenedor--subcontenedor {
    position: absolute;
    top: 100%;
    display: none;
  }
}
.menu__contenedor--format {
  min-width: 100%;
}
.menu__contenedor--subenlace {
  background-color: #202d75;
  color: #FFFFFF;
  padding: 1.1rem 0;
  display: block;
  width: 100%;
  text-align: center;
}
.menu__contenedor--subenlace:hover {
  background-color: #B40202;
}

.activo {
  background-color: #B40202;
  color: #FFFFFF;
}

.principal {
  background-image: linear-gradient(rgba(57, 70, 89, 0.5), rgba(57, 70, 89, 0.5)), url("../img/background-image.png");
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 10rem;
}
@media (min-width: 768px) {
  .principal {
    padding: 20rem 0;
  }
}
.principal__titulo {
  font-family: "Arima", cursive;
  font-weight: 500;
  color: #FFFFFF;
  margin: 0 3rem;
  text-align: center;
}
.principal__titulo--span {
  display: block;
  font-family: "Satisfy", cursive;
}

.nosotros {
  padding-top: 1rem;
  min-width: 100%;
  background-color: #FFFFFF;
}
.nosotros__header {
  text-align: center;
  color: #283891;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .nosotros__header {
    font-size: 3.2rem;
  }
}
.nosotros__header--span {
  color: #B40202;
}
.nosotros__contenedor {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .nosotros__contenedor {
    width: 90%;
    display: grid;
    align-items: center;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1025px) {
  .nosotros__contenedor {
    width: 80%;
  }
}
.nosotros__video {
  width: 90%;
  margin: 0 auto;
  max-width: 80rem;
  padding-bottom: 56.25%;
  position: relative;
}
@media (min-width: 768px) {
  .nosotros__video {
    margin: 2rem auto;
  }
}
@media (min-width: 1025px) {
  .nosotros__video {
    margin: 4rem auto;
  }
}
.nosotros__video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.nosotros__texto {
  width: 90%;
  margin: 1rem auto 0 auto;
}
@media (min-width: 1025px) {
  .nosotros__texto {
    padding: 0 2rem;
  }
}
@media (min-width: 1025px) {
  .nosotros__parrafo {
    font-size: 2.4rem;
  }
}

.testimoniales {
  margin: 1rem 0;
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .testimoniales {
    padding: 0 4rem;
  }
}
.testimoniales__header {
  text-align: center;
  color: #283891;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .testimoniales__header {
    font-size: 3.2rem;
  }
}
.testimoniales__contenedor {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .testimoniales__contenedor {
    flex-direction: row;
    gap: 4rem;
  }
}
.testimoniales__card {
  background-color: #283891;
  border: 0.5rem solid #B40202;
  border-radius: 2rem;
  margin: 2rem;
  padding: 1rem;
}
.testimoniales__card--imagen {
  border: 0.3rem solid #FFFFFF;
  border-radius: 1.5rem;
  overflow: hidden;
  width: 100%;
  height: 30rem;
  margin: 0 auto;
}
.testimoniales__card--img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.testimoniales__card--info {
  margin: 1rem 0;
}
.testimoniales__card--nombre {
  color: #FFFFFF;
  padding: 0 1rem;
  margin-bottom: 1rem;
}
.testimoniales__card--texto {
  color: #FFFFFF;
  margin: 0;
  padding: 0 1rem;
  text-align: justify;
  text-align-last: center;
}

.main {
  background-color: #FFFFFF;
}

.programas__header {
  text-align: center;
  background-color: #FFFFFF;
  color: #283891;
  padding: 4rem;
}
@media (min-width: 768px) {
  .programas__header {
    padding: 2rem;
  }
}
.programas__main {
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .programas__main {
    width: 85%;
  }
}
.programas__flex {
  display: flex;
  flex-direction: column;
}

.programa {
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .programa {
    margin-bottom: 5rem;
  }
}
.programa:first-child {
  margin-top: 2rem;
}
.programa__flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.programa__header {
  color: #B40202;
}
.programa__container {
  display: flex;
  flex-direction: column-reverse;
  gap: 2rem;
  width: 100%;
}
@media (min-width: 1025px) {
  .programa__container {
    flex-direction: row;
    gap: 4rem;
  }
}
@media (min-width: 1025px) {
  .programa__imagen {
    min-width: 50%;
  }
  .programa__imagen--img {
    min-height: 15rem;
  }
}
.programa__descripcion {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .programa__descripcion {
    justify-content: center;
  }
}
.programa__descripcion--header {
  font-family: "Satisfy", cursive;
  font-size: 2.5rem;
  color: #283891;
  text-align: center;
}
@media (min-width: 768px) {
  .programa__descripcion--header {
    font-size: 3rem;
  }
}
.programa__descripcion--texto {
  margin: 1rem 0;
  padding: 0 1rem;
  font-size: 1.8rem;
  text-align: justify;
}
@media (min-width: 768px) {
  .programa__descripcion--texto {
    font-size: 2rem;
  }
}
.programa__descripcion--list {
  list-style: disc;
}
.programa__descripcion--item {
  font-size: 1.8rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .programa__descripcion--item {
    font-size: 2rem;
  }
}
.programa__descripcion--modalidad {
  margin: 0.5rem 0;
  padding: 0 1rem;
}
@media (min-width: 768px) {
  .programa__descripcion--modalidad {
    font-size: 2rem;
  }
}
.programa__descripcion--horario {
  margin: 0.5rem 0;
  padding: 0 1rem;
}
@media (min-width: 768px) {
  .programa__descripcion--horario {
    font-size: 2rem;
  }
}
.programa__descripcion--deside {
  margin: 1rem 0;
  color: #B40202;
  font-size: 2rem;
  padding: 0 1rem;
}
@media (min-width: 768px) {
  .programa__descripcion--deside {
    font-size: 2rem;
  }
}

.contacto {
  background-color: #FFFFFF;
}
.contacto__header {
  padding: 3rem;
  text-align: center;
  color: #283891;
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .contacto__header {
    padding: 2rem;
  }
}
.contacto__titulo {
  color: #B40202;
  font-size: 2.5rem;
}
.contacto__titulo--dos {
  color: #B40202;
  font-size: 2.5rem;
  width: 90%;
  margin: 0 auto;
}
.contacto__informacion {
  width: 90%;
  margin: 0 auto;
  font-size: 2rem;
  padding-bottom: 2rem;
}
@media (min-width: 1025px) {
  .contacto__informacion {
    width: 85%;
    margin: 0 auto;
  }
}
.contacto__informacion--span {
  color: #B40202;
  text-decoration: underline;
}
.contacto__informacion--info {
  margin: auto 0;
}
.contacto__flex {
  display: flex;
  gap: 1rem;
}
.contacto__container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1025px) {
  .contacto__container {
    flex-direction: row;
    width: 90%;
    margin: 0 auto;
  }
}
.contacto__formulario {
  margin: 0 auto;
}
.contacto__mapa {
  width: 90%;
  margin: 0 auto;
  padding-bottom: 2rem;
}
.contacto__mapa--map {
  margin-top: 1rem;
  border-radius: 1.5rem;
}

.footer {
  background-color: #283891;
}
.footer__mas {
  font-size: 2rem;
  text-align: center;
  color: #FFFFFF;
  margin: 0 2rem;
  padding: 2.5rem 0;
}
.footer__mas--enlace {
  color: #FFFFFF;
  text-decoration: underline;
}
.footer__mas--enlace:hover {
  color: #B40202;
}
.footer__texto {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
  padding: 1.5rem 0;
}